
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

* {
		margin: 0;
		padding: 0;
}
*:focus {
		outline: none;
}
body {
		font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
		-moz-user-select: none;
		/*火狐*/
		-webkit-user-select: none;
		/*webkit浏览器*/
		-ms-user-select: none;
		/*IE10*/
		/*早期浏览器*/
		user-select: none;
}
div {
		list-style: none;
		-moz-user-select: none;
		 -ms-user-select: none;
		     user-select: none;
		-webkit-user-select: none;
}
ul,
	li {
		list-style: none;
		-moz-user-select: none;
		 -ms-user-select: none;
		     user-select: none;
		-webkit-user-select: none;
}
li {
		text-align: center;
}
em {
		font-style: normal;
}
.mainBK {
  width: 100%;
  height: 100%;
  background-color: #fbfbfb;
  overflow: hidden;
}
.policy-holderDiv {
  width: 100%;
  height: 400px;
  background-color: white;
  position: relative;
}
.titleFontClass {
  font-size: 16px;
  font-family: PingFangSC-Medium;
  font-weight: 500;
  color: rgba(51, 51, 51, 1);
}
.textFontClass {
  font-size: 14px;
  font-family: PingFangSC-Regular;
  font-weight: 400;
  color: rgba(51, 51, 51, 1);
}
.remarkTitleFontClass {
  font-size: 14px;
  font-family: PingFangSC-Medium;
  font-weight: 500;
  color: rgba(153, 153, 153, 1);
}
.remarkFontClass {
  font-size: 12px;
  font-family: PingFangSC-Regular;
  font-weight: 400;
}
.claimsRemarkFontClass {
  font-size: 11px;
  font-family: PingFangSC-Regular;
  font-weight: 400;
}
.remarkColor {
  color: #999999;
}
.readColor {
  color: #333333;
}
.holderTitleDiv {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid rgba(235, 235, 235, 1);
}
.holderTitleClass {
  left: 20px;
  right: 20px;
  top: 15px;
  height: 23px;
  position: absolute;
  line-height: 23px;
  text-align: left;
}
.holderUL {
  width: 100%;
  height: 400px;
}
.holderUL li {
  width: 100%;
  height: 50px;
}
.holderULlidiv {
  width: 100%;
  height: 50px;
  position: relative;
}
.inputTitleClass {
  left: 20px;
  width: 80px;
  top: 15px;
  height: 20px;
  position: absolute;
  line-height: 20px;
  text-align: left;
}
.inputTextClass {
  left: 110px;
  top: 15px;
  height: 20px;
  position: absolute;
  line-height: 20px;
  text-align: left;
  background-color: transparent;
  border: 0;
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
}
.holderLineClass {
  left: 20px;
  right: 20px;
  top: 49px;
  height: 1px;
  position: absolute;
  background-color: #e5e5e5;
}
